
import axios from "axios";
import wx from "weixin-js-sdk";
export default {
    ajaxGet(api, cb) {

        axios.get(api)
            .then(cb)
            .catch(err => {
                console.log(err);
            })
    },
    ajaxPost(api, post, cb) {
        axios.post(api, post)
            .then(cb)
            .catch(err => {
                console.log(err);
            })

    },

    my_alert(that, str, tit, cd) {
        that.$confirm(str, tit, {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).then(cd)
            .catch(() => {
                that.$message({
                    type: 'info',
                    message: ''
                });
            });
    }
    , wexinPay(data, cb, errorCb) {
        //
        let appId = data.appId;
        let timestamp = data.timeStamp;
        let nonceStr = data.nonceStr;
        let signature = data.signature;
        let packages = data.package;
        let paySign = data.paySign;
        let signType = data.signType;
        console.log('')
        //
        wx.config({
            debug: false, // ,apialertpclogpc
            appId: appId, // 
            timestamp: timestamp, // 
            nonceStr: nonceStr, // 
            signature: signature, // 1
            jsApiList: ['chooseWXPay'] // JSJS2
        });
        wx.ready(function () {
            wx.chooseWXPay({
                timestamp: timestamp, // jssdktimestamptimeStampS
                nonceStr: nonceStr, //  32 
                package: packages, // prepay_idprepay_id=***
                signType: signType, // 'SHA1''MD5'
                paySign: paySign, // 
                success: function (res) {
                    // 
                    cb(res);
                },
                fail: function (res) {
                    //
                    errorCb(res);
                }
            });
        });
        wx.error(function (res) {
            // configerrorconfigdebugresSPA
            /*alert("config");*/
        });
    }
}




