<template>
  <div class="box">
    <div v-if="vipshow" class="beizhao">
    </div>
    <div >
      <div class="top">
        <div class="user_box">
          <img :src="user_data.avater" alt>
          <div>
            <p>{{user_data.name}}</p>
            <!-- <p>:</p> -->
            <p v-if="user_data.father">:{{user_data.father.father_name.name}}</p>
          </div>
         
         
        </div>
         <div class="vip_boxs">
           <p><span></span>{{user_data.tixian_sum}}</p>
           <p  @click="go_balancelis"><span></span>{{user_data.balances}}</p>
           <p @click="go_kalman"></p>
            <p @click="show">{{isshow?'':''}}</p>
          </div>

      </div>
      <ul  class="vip_box clearfix">
        <li v-if="user_data.father" style="border-right:1px solid #ccc">
          <p></p>
          <img :src="user_data.father.father_name.avater" alt>
          <span  >{{user_data.father.father_name.name}}</span>
        </li>
        <li style="border-right:1px solid #ccc">
          <p></p>
          <img :src="user_data.avater" alt>
        </li>
        <li @click="go_url" style>
          <p>/</p>
          <span>{{user_data.children_count}}</span>
        </li>
      </ul>
      <div class="tixian" v-if="isshow">
          <el-select v-model="tixian_type" placeholder=""  style="display:inline-block;width:66%;">
            <el-option  label="" :value="1"></el-option>
            <el-option  label="" :value="2"></el-option>
          </el-select>
          <el-input   type="number" v-model="price" placeholder=""  @keyup.native="proving" clearable prefix-icon='el-icon-news' style="width:66%;" ></el-input>
          <el-button @click="is_tixian" type="primary" style="width:30%" ></el-button>
          <p style="color:#ccc;margin:3px 0">ps:<i style="color:#f34040;display:inline-block;margin:0 3px;">{{user_data.tixian_bili}}%</i>;10</p>
          <p style="color:#ccc;margin:3px 0">ps:<i v-for="(items,index) in user_data.tixian_date" :key="index" style="color:#f34040;display:inline-block;margin:0 0 0 3px;">{{items}}</i></p>
          <p style="color:#ccc;margin:3px 0">ps:;;0</p>
      </div>
        <div class="city">
      <citycom
        :province_ids="updata_data.province"
        :city_ids="updata_data.city"
        :district_ids="updata_data.district"
        v-on:province_id="province_ids"
        v-on:city_id="city_ids"
        v-on:district_id="district_ids"
      ></citycom>
      <div class="xiangxi">
        <span>&nbsp;:</span>
        <el-input
          v-model="detailed"
          placeholder=""
          size="mini"
          style="display:inline-block;width:75%"
        ></el-input>

      </div>
      <div class="xiangxi">
        <span>&nbsp;:</span>
        <el-input
          v-model="real_name"
          placeholder=""
          size="mini"
          style="display:inline-block;width:75%"
        ></el-input>
      </div>
      <div class="xiangxi">
        <span>&nbsp;:</span>
        <el-input
          v-model="mobile"
          placeholder=""
          type="number"
          size="mini"
          style="display:inline-block;width:75%"
        ></el-input>
      <p style="color:#ccc;margin:3px 0">ps:</p>
      </div>
    </div>
      <div class="zhifu">
        <img :src="url + user_data.kalman_pic" alt>
        <p>{{user_data.kalman_name}}</p>
        <h6>{{user_data.kalman_price}} <span style="float: right">:{{user_data.kalman_count}}</span></h6>
        <div style="font-size:12px;color:#909399">
          <el-checkbox v-model="tongyi" style="margin:0 6px  12px 6px;"></el-checkbox>
          <span @click="go_agreement">,()</span>
        </div>
        <el-input-number
          v-model="number"
          @change="handleChange"
          :min="0"
          :max="user_data.kalman_count"
          label=""
          size="small"
        ></el-input-number>
        <div style="float:right">
          <el-button @click="my_btn(1)" type="danger" size="small" style="margin:0;"  :disabled = 'iszhifu' ></el-button>
          <el-button @click="my_btn(2)" type="success" size="small" style="margin:0;"  :disabled = 'iszhifu' ></el-button>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import vip from "./vip";
export default vip;
</script>

<style lang="scss" scoped>
.box {
  padding-bottom: 60px;
  // background: #f3f0f0;
  .beizhao{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    z-index: 100000;
  }
  .tixian{
    width: 90%;
    margin: 18px auto;
  }
  .vip_quanli{
    margin: 18px auto 0;
    width: 90%;
    border-radius: 18px;
  }
  .city {
    width: 92%;
    margin: 12px auto;
    font-size: 14px;
    text-align: left;
    .xiangxi {
      margin: 6px auto;
      p{
        font-size:12px;
      } 
      span{
          display: inline-block;
          width: 22%;
      }

    }
  }
  .top {
    margin: 18px auto 0;
    height: 188px;
    width: 96%;
    border-radius: 18px;
    background: url("../../../static/images/bg.png") no-repeat;
    background-size: 100% 100%;
    position: relative; 
      .vip_boxs {
        position: absolute;
        bottom: 8px;
        text-align: center;
        width: 100%;
        font-size: 16px;
        color: #fff;
        // height: 38px;
        p{
          display: inline-block;
          width: 49%;
          font-size: 16px;
          font-weight: 700;
          margin-top:6px; 
          span{
            display: block;
            margin-bottom:3px; 
            font-weight: 400;
          }
        }
        p:last-child{
          font-weight: 400;
          font-size: 14px;
        }
        p:nth-child(3){
          font-weight: 400;
          font-size: 14px;
        }
      }
    .shenqing{
       position: absolute;
        bottom: 26px;
        left: 0;
        text-align: center;
        color: #fff;
        width: 100%;
        font-size: 20px;
        font-weight: 700;
    }
    .user_box {
      position: relative;
      height: 100%;
      width: 100%;
     
      img {
        width: 88px;
        height: 88px;
        border-radius: 50%;
        position: absolute;
        top: 28px;
        left: 28px;
      }
      div {
        position: absolute;
        top: 40px;
        left: 128px;
        font-size: 16px;
        font-weight: 700;
        color: #fff;
        p {
          margin-top: 12px;
        }
      }
    
    }
  }
  .vip_box {
    margin: 18px auto;
    width: 96%;
    li {
      width: 33%;
      text-align: center;
      float: left;
      p {
        font-size: 14px;
      }
      span {
        color: #f34040;
        display: block;
      }
      img {
        display: inline-block;
        margin: 6px auto;
        width: 58px;
        height: 58px;
        border-radius: 50%;
        border: 1px solid #ccc; /*no*/
      }
    }
    li:last-child {
      span {
        height: 78px;
        line-height: 78px;
        font-size: 18px;
        font-weight: 700;
      }
    }
  }
  .zhifu {
    width: 90%;
    margin: 0 auto;
    padding-bottom:28px; 
    font-size: 14px;
    img {
      width: 100%;
      display: block;
    }
    p {
      height: 22px;
      line-height: 22px;
    }
    h6 {
      height: 22px;
      line-height: 22px;
      color: #f34040;
      margin-bottom: 6px;
    }
  }
}
</style>



